import React from 'react';
import Link from 'gatsby-link';

class Speaking extends React.Component {
  render() {
    return (
      <div>
        <p>
          Since 2006, I've had a pleasure to give presentations about various
          topics about web accessibility, responsive web design, and working
          remotely.
        </p>
        <p>
          If you'd like me to speak at your event, just{' '}
          <a href="mailto:speaking@wojtekzajac.com">get in touch.</a>
        </p>
      </div>
    );
  }
}

export default Speaking;



